    .coin{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .coin-data{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .red{
        color: #f00606;
    }
    .green{
        color: #11d811;
    }
    .coin-row{
        margin-left: 1px;
        margin-right: 10px;
        box-shadow: 0 3px 3px rgba(0,0,0,0.5);
        
        margin-bottom: 40px;
        width: 330px;
    }
    
    .coin-text{
        margin-top: 60px;
        font-size: 50px;
        font-weight: bold;
        margin-bottom: 60px;
    }
